.card-link-images-cards-container {
    display: flex;
    
    .card-link-image-container {
        display: flex;
        width: 360px;
        height: 96px;
        background: #fff;
        box-shadow: 0px 4px 16px rgba(4, 115, 234, 0.2);
        border-radius: 16px;
        margin-right: 30px;

        &:hover {
            box-shadow: 0px 0px 0px 4px #0473ea;
            border-radius: 16px;
        }

        .card-link-img-container {
            width: 96px;
            background-size: cover;
            border-radius: 16px 0 0 16px;
        }
        .text-only-blue-cta {
            position: relative;
            top: 30px;
            margin-left: 12px;
            flex: 2;

            &:after {
                float:right;
                position: relative;
                top: 0;
                right: 0;
            }
        }
    } 
}

@media screen and (max-width: 767px) {
    .card-link-images-cards-container {
        display: block;
        
        .card-link-image-container {
            width: 345px;
            margin: 16px auto;

            .text-only-button {
                top: 1px;
            }

            p {
                margin: 0 0 0 12px;
            }

            &:hover {

                .text-only-button {

                    &.is-style-blue-only-text-download {

                        &:after {
                            top: 1px;
                        }
                    }

                    &.is-style-blue-only-text-play {

                        &:after {
                            top: 1px;
                        }
                    }

                    &.is-style-blue-only-text-external-link {

                        &:after {
                            top: 1px;
                        }
                    }
                }
            }
        } 
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .card-link-images-cards-container {
        display: flex;
        
        .card-link-image-container {
            display: flex;
            width: 345px;
            height: 48px;
            background: #fff;
            box-shadow: 0px 4px 16px rgba(4, 115, 234, 0.2);
            border-radius: 16px;
            margin-right: 15px;

            .card-link-img-container {
                width: 48px;
                background-size: cover;
                border-radius: 16px 0 0 16px;
            }
            .text-only-blue-cta {
                position: relative;
                top: 15px;
                margin-left: 12px;
                flex: 2;

                &:after {
                    float:right;
                    position: relative;
                    top: -3px;
                    right: 0;
                }
            }
        } 
    }
}