.single-featured-card-container {
    width: 555px;
    background: #FFFFFF;
    box-shadow: 0px 8px 16px rgba(11, 86, 168, 0.2);
    border-radius: 16px;
    margin-right: 30px;
    padding: 24px;

    &:last-child {
        margin-right: 0;
    }

    &.slick-slide {
        width: 555px;
    }

    .single-featured-main-text {
    	font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 28px;
        line-height: 36px;
        color: #525355;
        margin: 8px 0px;
        width: 507px;
    }

    .single-featured-paragraph {
    	font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
    	font-style: normal;
    	font-weight: normal;
    	font-size: 18px;
    	line-height: 27px;
    	color: #525355;
    }

    &:hover {
        background: #0473EA; 
        box-shadow: 0px 32px 64px rgba(11, 86, 168, 0.2);
        transition: all .3s;

        .single-featured-main-text {
            color: #fff;
            text-decoration: underline;
        }

        .single-featured-paragraph {
            color: #fff;
        }

        .text-only-button {
            transition: all .3s;
            color: #fff;
            font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 1.2;
            text-decoration: underline;

            &:after {
                color: #fff;
            }

            &.is-style-blue-only-text-download {

                &:after {
                    background-image: url(../src/images/download-white.svg);
                    content: '' ;
                    background-repeat: no-repeat;
                    background-size: 18px; 
                    display: inline-block;
                    position: relative;
                    width: 50px;
                    height: 18px;
                    top: 4px;
                    right: -5px;
                    transition: all .3s;
                }
            }

            &.is-style-blue-only-text-play {

                &:after {
                    background-image: url(../src/images/play-circle-fill-white.svg);
                    content: '' ;
                    background-repeat: no-repeat;
                    background-size: 18px; 
                    display: inline-block;
                    position: relative;
                    width: 50px;
                    height: 18px;
                    top: 4px;
                    right: -5px;
                    transition: all .3s;
                }
            }

            &.is-style-blue-only-text-external-link {

                &:after {
                    background-image: url(../src/images/external-link-white.svg);
                    content: '' ;
                    background-repeat: no-repeat;
                    background-size: 18px; 
                    display: inline-block;
                    position: relative;
                    width: 50px;
                    height: 18px;
                    top: 4px;
                    right: -5px;
                    transition: all .3s;
                }
            }
        }

        a {
            color: #fff;
            text-decoration: underline;

            &:after {
                color: #fff;
            }
        }
    }
}


@media screen and (max-width: 767px) {
    
    .single-featured-card-container {
        width: 265px;
        margin: 1em auto;
        padding: 24px;

        &.slick-slide {
            width: 265px;
            margin-right: 16px;
        }

        &:last-child {
            margin: 1em auto;
        }


        .single-featured-main-text {
            font-size: 23px;
            line-height: 30px;
            width: 217px;
        }

        .single-featured-paragraph {
            font-size: 16px;
            line-height: 24px;
        }

        &:hover {

            .text-only-button {
                font-size: 16px;
                line-height: 24px;
            }
        }
    } 
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .single-featured-card-container {
        width: 282px;

        &.slick-slide {
            width: 282px;
        }

        &:last-child {
            margin-right:0;
        }

        .single-featured-main-text {
            font-size: 23px;
            line-height: 30px;
            width: 234px;
        }

        .single-featured-paragraph {
            font-size: 16px;
            line-height: 24px;
        }

        &:hover {

            .text-only-button {
                font-size: 16px;
                line-height: 24px;
            }
        }
    } 
}