.single-standard-card-container {
  height: auto;
  background: #FFFFFF;
  box-shadow: 0px 4px 16px rgba(82, 83, 85, 0.20);
  border-radius: 16px;
  margin-right: 30px;
  margin-bottom: 16px;
  margin-top: 16px;
  padding-bottom: 24px;

  &.slick-slide {
  	width: 263px;
  }

	img {
		width: 263px;
		height: 149px;
		border-radius: 16px 16px 0 0;
		object-fit: cover;
	}

	a {
  	text-decoration: none;
  }

  &:hover {
  	box-shadow: 0px 0px 0px 4px #0473ea;
  	border-radius: 16px;
  }

	.single-standard-card-main-text {
		font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: #525355;
    margin: 24px;
    width: 312px;
	} 

	.single-standard-card-paragraph {
  	font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
  	font-style: normal;
  	font-weight: normal;
  	font-size: 18px;
  	line-height: 27px;
  	color: #525355;
  	padding: 0 24px;
  }	

	&.is-style-large {
		width: 555px;
    background: #FFFFFF;
    box-shadow: 0px 4px 16px rgba(82, 83, 85, 0.20);
    border-radius: 16px;
    margin-right: 30px;
  	margin-bottom: 16px;
  	margin-top: 16px;
    padding-bottom: 24px;

    &.slick-slide {
    	width: 555px;
    }

		img {
			width: 555px;
			height: 312px;
			border-radius: 16px 16px 0 0;
			object-fit: cover;
		}

		&:hover {
	  	box-shadow: 0px 0px 0px 4px #0473ea;
	  	border-radius: 16px;
  	}

		.single-standard-card-main-text {
			font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
	    font-style: normal;
	    font-weight: 500;
	    font-size: 28px;
	    line-height: 36px;
	    color: #525355;
	    margin: 24px;
	    width: 312px;
		}

		.single-standard-card-paragraph  {
    	font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
    	font-style: normal;
    	font-weight: normal;
    	font-size: 18px;
    	line-height: 27px;
    	color: #525355;
    	padding: 0 24px;
    }
	}

	&.is-style-square {	
		width: 360px;	
    background: #FFFFFF;
    box-shadow: 0px 4px 16px rgba(82, 83, 85, 0.20);
    border-radius: 16px;
    margin-right: 30px;
	  margin-bottom: 16px;
	  margin-top: 16px;
    padding-bottom: 24px;

     &.slick-slide {
    	width: 360px;
    }

		img {
			width: 360px;
			height: 360px;
			border-radius: 16px 16px 0 0;
			object-fit: cover;
		}

		&:hover {
	  	box-shadow: 0px 0px 0px 4px #0473ea;
	  	border-radius: 16px;
	  }

		.single-standard-card-main-text {
			font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
	    font-style: normal;
	    font-weight: 500;
	    font-size: 28px;
	    line-height: 36px;
	    color: #525355;
	    margin: 24px;
	    width: 312px;
		}

		.single-standard-card-paragraph {
    	font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
    	font-style: normal;
    	font-weight: normal;
    	font-size: 18px;
    	line-height: 27px;
    	color: #525355;
    	padding: 0 24px;
    }
	}

	ul {
		display: flex;
		list-style-type: none;
		padding: 0;
		margin: 0 24px;

		.hero-category {
			padding: 4px 8px;
			height: 26px;
			background: #EBFAE5;
			border-radius: 14px;
			margin-right: 10px;
			min-width: 33px;
			text-align: center;

			a {
				font-family: "SC Prosper Sans Regular", "Helvetica", "Arial", "Lucida Grande", sans-serif;
				font-style: normal;
				font-weight: bold;
				font-size: 12px;
				line-height: 24px;
				color: #1B6600;
				text-decoration: none;
			}
		}
	}
}


@media screen and (max-width: 767px) {
	
	.single-standard-card-container {
    margin: 1em auto;
    width: 265px;
    margin-right: 16px;

    &.slick-slide {
    	width: 265px;
    	margin-right: 16px;
    }

		img {
			width: 265px;
			height: 150px;
			object-fit: cover;
		}

		.single-standard-card-main-text {
		    font-size: 24px;
		    line-height: 30px;
		    width: 233px;
		} 

		.single-standard-card-paragraph {
	    	font-size: 16px;
	    	line-height: 24px;
	    }	

		&.is-style-large {
			margin: 1em auto;
			width: 305px;

			&.slick-slide {
	    	width: 305px;
	    	margin-left: 16px;
	    }

			img {
				width: 305px;
				height: 171px;
				object-fit: cover;
			}

			.single-standard-card-main-text {
			    font-size: 24px;
			    line-height: 30px;
			    width: 273px;
			}

			.single-standard-card-paragraph  {
		    	font-size: 16px;
	    		line-height: 24px;
		    }
		}

		&.is-style-square {			
	    width: 305px;
	    margin: 1em auto;

		  &.slick-slide {
	    	width: 305px;
	    	margin-right: 16px;
	    }

			img {
				width: 305px;
				height: 305px;
				border-radius: 16px 16px 0 0;
				object-fit: cover;
			}

			.single-standard-card-main-text {
			    font-size: 24px;
			    line-height: 30px;
			    width: 273px;
			}

			.single-standard-card-paragraph {
		    	font-size: 16px;
	    		line-height: 24px;
		    }
		}

		ul {
			display: flex;
			list-style-type: none;
			padding: 0;
			margin: 0 24px;

			.hero-category {
				padding: 2px 6px;
				height: 21px;
				border-radius: 14px;
				min-width: 42px;

				a {
					font-size: 11px;
					line-height: 16px;
				}
			}
		}
	}
}

